.header__contacts-link {
  display: flex;
  align-items: center;
  column-gap: 16px;
  color: #f2f5f6;
  font-family: "PT Mono", "Times New Roman", serif;
  font-size: 16px;
  line-height: 18px;
  padding-left: 5px;
}
.header__contacts-link:active svg {
  opacity: 0.6;
}
@media (hover: hover) {
  .header__contacts-link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.header__contacts-link.focus-visible:focus {
  opacity: 0.8;
}

.header__contacts-link span {
  align-self: baseline;
}
@media (max-width: 1023px) {
  .header__contacts-link span {
    display: none;
  }
}

.header__button {
  color: #f2f5f6;
  background-color: #041827;
  width: 160px;
  min-height: 40px;
  border: 1px solid #f2f5f6;
  border-radius: 3px;
  padding: 5px 15px 12px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  line-height: 17px;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 14px;
  font-weight: 700;
  cursor: pointer;
}
@media (max-width: 1023px) {
  .header__button {
    display: none;
  }
}
@media (hover: hover) {
  .header__button:not(:disabled):hover:not(.focus-visible) {
    border: 1px solid #00b2ff;
    color: #00b2ff;
  }
}
.header__button:not(:disabled).focus-visible:focus {
  border: 1px solid #00b2ff;
  color: #00b2ff;
}
.header__button:active {
  opacity: 0.9;
}
.header__button:disabled {
  opacity: 0.2;
}

