.footer__button {
  border: none;
  background-color: inherit;
  padding: 0;
  text-align: left;
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  letter-spacing: 1px;
  margin: 0;
  margin-bottom: 20px;
  text-transform: uppercase;
  letter-spacing: 0.2px;
}
@media (max-width: 1023px) {
  .footer__button {
    font-size: 15px;
    line-height: 16px;
  }
}
@media (max-width: 766px) {
  .footer__button {
    margin-bottom: 15px;
    width: 100%;
  }
  .footer__button--closed {
    position: relative;
  }
  .footer__button--closed::before {
    content: "";
    position: absolute;
    width: 15px;
    height: 1px;
    background-color: #f8fdff;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
  }
  .footer__button--closed::after {
    content: "";
    position: absolute;
    width: 1px;
    height: 15px;
    background-color: #f8fdff;
    right: 6.5px;
    top: 0;
  }
  .footer__button--opened {
    position: relative;
  }
  .footer__button--opened::before {
    content: "";
    position: absolute;
    width: 15px;
    height: 1px;
    background-color: #f8fdff;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
  }
}

