.footer__contacts-item a:hover:not(.focus-visible),
.footer__contacts-item address:hover:not(.focus-visible) {
    color: #f2f5f6;
  }
}
.footer__nav-item a.focus-visible:focus,
.footer__contacts-item a.focus-visible:focus,
.footer__contacts-item address.focus-visible:focus {
  color: #f2f5f6;
}
.footer__nav-item a:active,
.footer__contacts-item a:active,
.footer__contacts-item address:active {
  color: #f2f5f6;
  opacity: 0.9;
}

@media (max-width: 766px) {
  .footer__contacts-item address {
    padding-bottom: 5px;
  }
}

